<template>
  <div>
      <!-- new form -->
      <div class="needs-validation" v-show="nav.options.showformu">
        <div class="form-row">
            <div class="header row p-2  col-12" :class="headerclass">
                <div class="col-md-3 mb-3">
                    <label class="h3" for="headerchoose">en-tete</label> <br>
                    <label for="headerchoose" class="px-2">selectioner</label>
                    <input type="checkbox" name="headerchoose" id="headerchoose" v-model="headerchoose">
                </div>
                
                <div class="col-md-4 mb-3">
                <label for="validationCustomUsername">Nom</label>
                <div class="input-group">
                    <div class="input-group-prepend">
                    <span class="input-group-text" id="oooo"><i class="fas fa-user"></i></span>
                    </div>
                    <input type="text" class="form-control" id="ooooo" aria-describedby="inputGroupPrepend" v-model="info.name" v-on:keypress='update_formu' required>
                </div>
                </div>
                <div class="col-md-4 mb-3">
                <label for="validationCustomUsername">Prenom</label>
                <div class="input-group">
                    <div class="input-group-prepend">
                    <span class="input-group-text" id="uuuuuu"><i class="fas fa-user"></i></span>
                    </div>
                    <input type="text" class="form-control" id="uu" aria-describedby="inputGroupPrepend" v-model="info.username" v-on:keypress='update_formu' required>
                    
                </div>
                </div>
            </div>
            <div class="header row p-2  col-12" :class="bodyclass">
                <div class="col-md-2 mb-2">
                    <label class="h3" for="headerchoose2">corps </label>
                    <label for="headerchoose2" class="px-2"> selectioner </label>
                    <input type="checkbox" name="headerchoose2" id="headerchoose2" v-model="bodychoose">
                </div>
                <div class="col-md-6 mb-3">
                    <label for="validationTextarea">Presentez vous</label>
                    <textarea class="form-control " id="validationTextarea"  v-model="info.description" required></textarea>   
               </div>
               <Dmodal/>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'
let Dmodal=require('./diplomeModal.vue').default
export default {
    store:store,
    components:{
        'Dmodal':Dmodal
    },
    data:function(){
        return {
            nav:this.$store.state.nav,
            info:this.$store.state.info,
            history:this.$store.state.history,
            headerchoose:this.$store.state.header.selected.headerchoose,
            headerclass:this.$store.state.header.selected.headerclass,
            headerclassfull:this.$store.state.header.selected.headerclassfull,

            bodychoose:this.$store.state.body.selected.choose,
            bodyclass:this.$store.state.body.selected.class,
            bodyclassfull:this.$store.state.body.selected.classfull,
            
            diplomeall:this.$store.state.body.diplome.all,
            cur_diplome:'',
        }
    },
    methods:{
        add_diplome(){
            if (this.cur_diplome.length<2) return 0
            this.diplomeall.push(this.cur_diplome)
            this.cur_diplome=''
        },
        update_formu:function(){
                this.history.index.name=(this.history.name.length-1)
                this.history.index.username=(this.history.username.length-1)
                this.history.name.push(this.info.name)
                this.history.username.push(this.info.username)
            }
    },
    watch:{
        headerchoose:function(){
           this.headerchoose?this.headerclass=this.headerclassfull:this.headerclass=[]
           this.$parent.header.selected.headerchoose=this.headerchoose
           
        },
        bodychoose:function(){
            this.bodychoose?this.bodyclass=this.bodyclassfull:this.bodyclass=[]
            this.$parent.body.selected.choose=this.bodychoose
        },
    }

}
</script>

