<template>
<div class="col-2">
    <select id="inputState" class="form-control col-12 mx-3" v-model="cur_size" aria-placeholder="taille" >
           <template v-for="element in all_size" >
                <option v-bind:value="element+'px'" :key="element.id" :style="{'font-family':element}">{{element}}px</option>
            </template>
    </select>
</div>
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            all_size:[],
            cur_size:'',
            header:this.$store.state.header,
            body:this.$store.state.body,
            
        }
    },
    mounted(){
        let  max=50
        console.log('monter');
        for (let index = 10; index <= max; index++) {
            let str_index=index.toString()
            this.all_size.push(str_index)
        }
    },
    watch:{
        cur_size:function(){
             if (this.body.selected.choose==true) {
                this.body.text['font-size']=this.cur_size
             }
             if (this.header.selected.headerchoose==true) {
                this.header.text['font-size']=this.cur_size
             }
        }
    },
    methods:{
        testor:function(){

        }
    }

}
</script>
<style scoped>
.btn3{
    color: blue;
}
