<template>
  <div  class="row my-3 shadow mb-4 mt-1 rounded-bottom p-1" >
      <button class="btn btn-primary m-1" @click="save">Save 
          <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true" v-show="spiner1"></span>
            <span class="sr-only">...</span>
      </button>
      
      <button class="btn btn-danger m-1" @click="load" >Load
          <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true" v-show="spiner2"></span>
            <span class="sr-only">...</span>
      </button>
  </div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'
import axios from 'axios'
export default {
    store:store,
    data:function(){
        return{
            info:this.$store.state.info,
            header:this.$store.state.header,
            body:this.$store.state.body,
            spiner1:false,
            spiner2:false,
            axios:axios
        }
    },
    methods:{
        save:function(){
            this.spiner1=true

            sessionStorage.setItem('name',this.info.name)
            sessionStorage.setItem('username',this.info.username)
            sessionStorage.setItem('description',this.info.description)

            sessionStorage.setItem('bodyBg',this.body.text.background)
            sessionStorage.setItem('bodyCol',this.body.text.color)
            sessionStorage.setItem('bodyfontfam',this.body.text['font-family'])
            sessionStorage.setItem('bodyOpa',this.body.text.opacity)

            sessionStorage.setItem('headBg',this.header.text.background)
            sessionStorage.setItem('headCol',this.header.text.color)
            sessionStorage.setItem('headfontfam',this.header.text['font-family'])
            sessionStorage.setItem('headOpa',this.header.text.opacity)

            setTimeout(() => {
                this.spiner1=false
            },1000);
            
        },
        load:function(){
            this.spiner2=true
            
            this.info.name=sessionStorage.getItem('name')
            this.info.username=sessionStorage.getItem('username')
            this.info.description=sessionStorage.getItem('description')

            this.body.text.background=sessionStorage.getItem('bodyBg')
            this.body.text.color=sessionStorage.getItem('bodyCol')
            this.body.text['font-family']=sessionStorage.getItem('bodyfontfam')
            this.body.text.opacity=sessionStorage.getItem('bodyOpa')

            this.header.text.background=sessionStorage.getItem('headBg')
            this.header.text.color=sessionStorage.getItem('headCol')
            this.header.text['font-family']=sessionStorage.getItem('headfontfam')
            this.header.text.opacity=sessionStorage.getItem('headOpa')
            //////////////////

            this.spiner2=true
            setTimeout(() => {
                this.spiner2=false
            }, 1000);
        }
    }
}
</script>

