<template>
  <div class="col-md-4 mb-3">

    <div class="input-group ">
        <label class="h6 col-12"> {{title}}</label>
        <input type="text" class="form-control" disabled :placeholder="counter()" aria-label="" aria-describedby="button-addon2">
        <div class="input-group-append">
            <button class="btn btn-primary" type="button" id="button-addon2" data-toggle="modal" data-target="#exampleModal3">modifier</button>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="exampleModal3" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{title}}</h5> <span class="badge badge-info mx-2">{{counter()}}</span>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">
           <div class="container">
               <div class="row my-2">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="votre fonction" aria-label="" v-model="schema.fonction" aria-describedby="button-addon3">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="nom de la societe" aria-label="" v-model="schema.nom" aria-describedby="button-addon3">
                   <input type="date" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="date de debut" aria-label="" v-model="schema.date.debut" aria-describedby="button-addon3">
                   <input type="date" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="date de fin" aria-label="" v-model="schema.date.fin" aria-describedby="button-addon3">
                   <textarea name="" id=""  placeholder="Inserez ici un bref resume de vos responsabilites cles et de vos principales realisations." class="border-0 border-white col-12 my-3 mx-2" v-model="schema.commentaire"></textarea>
                   <button class="btn btn-info  mx-2 rounded-circle" @click="add()"><i class="fas fa-plus"></i></button>
               </div>
               <div class="row">
                   <div class="" v-for="experience in experiences" v-bind:key="experience.id">
                   <button class="btn btn-danger rounded-pill m-1">{{experience.nom}} <span class="text-light h6 font-weight-bold pl-1" @click="remove(experience)">x</span></button>
                    </div>   
               </div>
                       
           </div>
        </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/quickcv/index.js'

export default {
    store:store,
    data:function(){
        return{
            title:'Experiences',
            champ:'',
            info:store.state.info,
            experiences:store.state.info.experiences,
            schema:{
                fonction:'',
                nom:'',
                date:{
                    debut:'',
                    fin:'',
                },
                commentaire:''
            }
        }
    },
    methods:{
        add:function(){
            const schema =this.hardcopy(this.schema)
            this.experiences.push(schema)
            this.champ=''
            //this.champ=AudioBuffer()
        },
        hardcopy(obj){
            var copy = JSON.stringify(obj);
            const copy2=JSON.parse(copy)
            return copy2
        },
        remove:function(prop){
            const comp = prop
            let new_experiences=this.experiences.filter((element)=>{
                if (element!=comp) return element
            })
            this.experiences=new_experiences
        },
        counter:function(){
            const long = this.experiences.length
            return long
        }
    }

}
</script>

