<template>
  <input type="color"  v-model="textcolor" style="border-style:none,border-width:0px">
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return {
            textcolor:this.$store.state.textcolor,
            header:this.$store.state.header,
            body:this.$store.state.body
        }
    },
    watch:{
         textcolor:function(){
             if (this.body.selected.choose==true) {
                 this.body.text.color=this.textcolor
             }
             if (this.header.selected.headerchoose==true) {
                 this.header.text.color=this.textcolor
             }
            //this.info_style="color:"+this.textcolor
            //this.header.text.color=this.textcolor
            //console.log('changer')
        },
    }

}
</script>

