<template>
    <select id="inputState" class="form-control col-2 mx-3" v-model="policy" >
            <template v-for="element in policylist" >
                <option v-bind:value="element" :key="element.id" :style="{'font-family':element}">{{element}}</option>
            </template>
    </select>
  
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            policy:this.$store.state.policy,
            policylist:this.$store.state.policylist,
            header:this.$store.state.header,
            body:this.$store.state.body
        }
    },
    watch:{
        policy:function () {
            if (this.body.selected.choose==true) {
                this.body.text['font-family']=this.policy
             }
             if (this.header.selected.headerchoose==true) {
                this.header.text['font-family']=this.policy
             }
            //this.header.text['font-family']=this.policy
        },
    }

}
</script>

