<template>
  <div>
      <i class="fas fa-1x m-2 btn btn-info" :class="align_cur" @click="change"></i>
  </div>
</template>

<script>
import store from '../../../../store/cv_data/appState.js'

export default {
    store:store,
    data:function(){
        return{
            align_cur:'fa-align-center',
            align_pack:['fa-align-center','fa-align-justify','fa-align-left','fa-align-right'],
            align_class:['center','justify','left','right'],
            header:this.$store.state.header,
            body:this.$store.state.body
        }
    },
    watch:{
        align_cur:function(){
            let index= this.align_pack.indexOf(this.align_cur)
            //this.header.text['text-align']=this.align_class[index]
            if (this.body.selected.choose==true) {
                 this.body.text['text-align']=this.align_class[index]
             }
             if (this.header.selected.headerchoose==true) {
                this.header.text['text-align']=this.align_class[index]
             }
        }
    },
    methods:{
        change:function(){
            let index= this.align_pack.indexOf(this.align_cur)
            if (index==3) index=0
            else index++
            this.align_cur=this.align_pack[index]
            //console.log(this.align_pack.lenght);

        }
    }

}
</script>