<template>
  <div class="col-md-4 mb-3">
      <div >
            <label for="validationCustomUsername">diplome <span class="badge badge-info mx-2">{{diplen()}}</span></label>
            <div class="input-group">
                <input type="text" class="form-control " disabled id="ppp" aria-describedby="inputGroupPrepend" placeholder="diplome obtenu # annee" v-model="cur_diplome" required>
                <div class="input-group-prepend border-right">
                <button class="btn btn-primary rounded-right fas fa-plus" id="p" data-toggle="modal" data-target="#exampleModal" ></button>
                </div>
            </div>
        </div>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Diplome obtenu <span class="badge badge-info mx-2">{{diplen()}}</span></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">    
            <div class="col-md-12" title="ajouter et supprimer vos diplome">
                
                <div class="input-group">
                    <input type="text" class="form-control p-3" id="ppp" aria-describedby="inputGroupPrepend" placeholder="ajouter de nouvel diplome " v-model="cur_diplome" required>
                    <div class="input-group-prepend border-right">
                    <button class="btn btn-primary rounded-right fas fa-plus px-3" id="p" @click="add_diplome()" ></button>
                    </div>
                </div>
            </div>
            <div v-for="diplome in diplomeall" v-bind:key="diplome.id">
                  <p class="alert alert-info rounded p-3 text-left pl-5 my-2 h4"> {{diplome}}  <button class=" btn btn-danger fas fa-trash text-light ml-3" @click="del(diplome)"></button></p>
            </div>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Annuler</button>
            <button type="button" class="btn btn-primary">Enregistrer</button>
        </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            diplomeall:this.$store.state.body.diplome.all,
            cur_diplome:'',
        }
    },
     methods:{
        add_diplome(){
            if (this.cur_diplome.length<2) return 0
            this.diplomeall.push(this.cur_diplome)
            this.cur_diplome=''
        },
        del(parm){
            
            this.diplomeall=this.diplomeall.filter((element)=>{
                if (element!=parm) {
                    return element
                }   
            })
        },
        diplen(){
            return this.diplomeall.length
        }
     }

}
</script>

<style scoped>

</style>