<template>
  <div class="col-md-4 mb-3">

    <div class="input-group ">
        <label class="h6 col-12"> {{title}}</label>
        <input type="text" class="form-control" disabled :placeholder="counter()" aria-label="" aria-describedby="button-addon2">
        <div class="input-group-append">
            <button class="btn btn-primary" type="button" id="button-addon2" data-toggle="modal" data-target="#exampleModal">modifier</button>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{title}}</h5> <span class="badge badge-info mx-2">{{counter()}}</span>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">
           <div class="container">
               <div class="row my-2">
                   <input type="text" class="form-control rounded-pill col-6" placeholder="" aria-label="" v-model="champ" aria-describedby="button-addon3">
                   <button class="btn btn-info  mx-2 rounded-circle" @click="add()"><i class="fas fa-plus"></i></button>
               </div>
               <div class="row">
                   <div class="" v-for="competence in competences" v-bind:key="competence.id">
                   <button class="btn btn-info rounded-pill m-1">{{competence}} <span class="text-light h6 font-weight-bold pl-1" @click="remove(competence)">x</span></button>
                    </div>   
               </div>
                       
           </div>
        </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/quickcv/index.js'

export default {
    store:store,
    data:function(){
        return{
            title:'Competences',
            champ:'',
            info:store.state.info,
            competences:store.state.info.competences,
            count:'<span class="badge badge-info mx-2">{{diplen()}}</span>',
        }
    },
    methods:{
        add:function(){
            this.competences.push(this.champ)
            this.champ=''
        },
        remove:function(prop){
            const comp = prop
            let new_competences=this.competences.filter((element)=>{
                if (element!=comp) return element
            })
            this.competences=new_competences
        },
        counter:function(){
            const long = this.competences.length
            return long
        }
    }

}
</script>

