<template>
  <i class="fas fa-undo fa-1x m-2 btn btn-info" v-on:click='undo'></i>
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            info:this.$store.state.info,
            history:this.$store.state.history,

        }
    },
    methods:{
        undo:function(){
            let index_name = this.history.index.name==null?(this.history.name.length-1):this.history.index.name
            let index_username = this.history.index.username==null?(this.history.username.length-1):this.history.index.username
            if (index_name<0 || index_username<0) {
                return 0
            }
            this.info.name=this.history.name[index_name]
            this.info.username=this.history.username[index_username]
            index_name--
            index_username--
            this.history.index.name=index_name
            this.history.index.username=index_username
            //console.log(this.history.name)

        },
    }

}
</script>

