<template>
  <div class="col-md-4 mb-3">

    <div class="input-group ">
        <label class="h6 col-12"> {{title}}</label>
        <input type="text" class="form-control" disabled :placeholder="info.objectifs" :title="info.objectifs" aria-label="" aria-describedby="button-addon2">
        <div class="input-group-append">
            <button class="btn btn-primary" type="button" id="button-addon2" data-toggle="modal" data-target="#exampleModal2">modifier</button>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="exampleModal2" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{title}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">
           <textarea name="" id=""  placeholder="ici ..." class="border-0 border-white col-12" v-model="temp"></textarea>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="cancel()">Annuler</button>
            <button type="button" class="btn btn-primary" @click="save()">Enregister</button>
        </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/quickcv/index.js'

export default {
    store:store,
    data:function(){
        return{
            title:'objectif',
            temp:'',
            info:store.state.info,
        }
    },
    methods:{
        save:function(){
            this.info.objectifs=this.temp
        },
        cancel:function(){
            this.temp=this.info.objectifs
        }
    }

}
</script>

