<template>
  <input type="color" name="" id="" v-model="backcolor" style="border-style:none,border-width:0px">
</template>

<script>
import store from '../../../../store/cv_data/appState.js'

export default {
    store:store,
    data:function(){
        return {
            backcolor:this.$store.state.backcolor,
            header:this.$store.state.header,
            body:this.$store.state.body
        }
    },
    watch:{
         backcolor:function(){
             if (this.body.selected.choose==true) {
                 this.body.text.background=this.backcolor
             }
             if (this.header.selected.headerchoose==true) {
                 this.header.text.background=this.backcolor
             }
            //this.header.text.background=this.backcolor
        }
    }

}
</script>
