<template>
  <div class="col-md-4 mb-3">

    <div class="input-group ">
        <label class="h6 col-12"> {{title}}</label>
        <input type="text" class="form-control" disabled :placeholder="counter()" aria-label="" aria-describedby="button-addon2">
        <div class="input-group-append">
            <button class="btn btn-primary" type="button" id="button-addon2" data-toggle="modal" data-target="#exampleModal4">modifier</button>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="exampleModal4" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
        <div class="modal-content">
        <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{title}}</h5> <span class="badge badge-info mx-2">{{counter()}}</span>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            </button>
        </div>
        <div class="modal-body">
           <div class="container">
               <div class="row my-2">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="votre diplome" aria-label="" v-model="schema.diplome" aria-describedby="button-addon3">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="nom de l'ecole " aria-label="" v-model="schema.nom" aria-describedby="button-addon3">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="pays" aria-label="" v-model="schema.lieu.pays" aria-describedby="button-addon3">
                   <input type="text" class="form-control rounded-pill col-5 my-1 mx-2" placeholder="ville" aria-label="" v-model="schema.lieu.ville" aria-describedby="button-addon3">
                   <textarea name="" id=""  placeholder="Inserez ici un bref resume de votre formation" class="border-0 border-white col-12 my-3 mx-2" v-model="schema.commentaire"></textarea>
                   <button class="btn btn-info  mx-2 rounded-circle" @click="add()"><i class="fas fa-plus"></i></button>
               </div>
               <div class="row">
                   <div class="" v-for="experience in formations" v-bind:key="experience.id">
                   <button class="btn btn-danger rounded-pill m-1">{{experience.nom}} <span class="text-light h6 font-weight-bold pl-1" @click="remove(experience)">x</span></button>
                    </div>   
               </div>
                       
           </div>
        </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
import store from '../../../store/cv_data/quickcv/index.js'

export default {
    store:store,
    data:function(){
        return{
            title:'Formations',
            champ:'',
            info:store.state.info,
            formations:store.state.info.formations,
            schema:{
                diplome:'',
                nom:'',
                lieu:{
                    pays:'',
                    ville:'',
                },
                commentaire:''
            }
        }
    },
    methods:{
        add:function(){
            const schema =this.schema
            this.formations.push(schema)
            this.champ=''
        },
        remove:function(prop){
            const comp = prop
            let new_formations=this.formations.filter((element)=>{
                if (element!=comp) return element
            })
            this.formations=new_formations
        },
        counter:function(){
            const long = this.formations.length
            return long
        }
    }

}
</script>

<style scoped>

</style>