<template>
  <div>
      <h1 class="bg-info text-light text-center p-3 rounded-bottom shadow ">choissez un model</h1>
      <!-- model -->
      
      <div class="row">
          <div class="col-md-12  col-lg-5 m-3 shadow">
            <h5 class="card-header bg-info text-light mt-1 text-center">Lite blue</h5>
            <div class="card-body">
                <h5 class="card-title text-success">Gratuit</h5>
                <p class="card-text">theme assez basique et minimaliste de couleur par defaut blue clair et bien evidemment la couleur est personalisable </p>
                <button  class="btn btn-secondary mx-1" :class="choosed('liteBlue')" @click="choose('liteBlue')" >choisir</button>
                <button  class="btn btn-danger mx-1"><i class="fas fa-paint-brush"></i></button>
            </div>
            </div>

            <div class="card col-md-12  col-lg-5 m-3 shadow">
            <h5 class="card-header bg-dark text-light mt-1 text-center">Monster trak</h5>
            <div class="card-body">
                <h5 class="card-title text-danger">Payant</h5>
                <p class="card-text">theme gameur de derniere generation avec une customisation pousser</p>
                <button  class="btn btn-secondary mx-1" disabled :class="choosed('monsterTrik')" @click="choose('monsterTrik')">choisir</button>
                <button  class="btn btn-danger mx-1"><i class="fas fa-paint-brush"></i></button>
            </div>
            </div>

         </div>
         <div class="row">
<p class="col-12 h5 alert alert-warning m-3 animate__bounceIn text-center text-blod" id="ajaxerr2" v-if="showajaxerr"> veillez choisir un theme </p>
      </div>
        
     <!-- -->
     <div class="row m-5">
         <button class="btn btn-secondary m-1" @click="back()">retour</button>
        <button class="btn btn-info m-1" @click="submit()">suivant</button>
     </div>
      
  </div>
</template>

<script>
import store from '../../store/cv_data/quickcv/index.js'

export default {
    store:store,
    data:function(){
        return{
            info:store.state.info,
            steep:store.state.steep,
            themes:store.state.choosemodel.themes,
            choosed2:store.state.choosemodel.choosed2,
            showajaxerr:store.state.choosemodel.showajaxerr,
        }
    },
    methods:{
        back:function(){
            this.steep.now--
        },
        reload:function(){
            this.showajaxerr=true
            setTimeout(() => {
                this.showajaxerr=false
            }, 2000);
        },
        error:function(){
            if (this.choosed2=='none'){
                this.reload()
                return false
            }
            else return true
        },
        submit:function(){
            const result = this.error()
            result?this.steep.now++:0
            
        },
        choose:function(theme){
            this.themes[theme].choose=!this.themes[theme].choose
            this.choosed2=this.themes[theme].choose?theme:'none'
        },
        choosed:function(theme){
            if (this.themes[theme].choose==true) {
                return 'btn-info'
            }
            else{
                return 'btn-secondary'
            }

        },
    }

}
</script>

