<template>
  <div  class="row my-3 shadow mb-4 mt-1 rounded-bottom p-1" >
    <button class="btn btn-info m-2 themes_btn" @click="update('info')">hello!</button>
    <button class="btn btn-danger m-2 themes_btn2"  @click="update('danger')">hello!</button>
    <button class="btn btn-success m-2 themes_btn"  @click="update('success')">hello!</button> 
  </div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'

export default {
  store:store,
  data:function(){
    return{
      header:this.$store.state.header,
      cvmodel:this.$store.state.cvmodel,
      body:this.$store.state.body,
      temp:{
        header:{},
      }
    }
  },
  methods:{
    
    update:function(prop){
     this.updator(prop)
     console.log(this.header.selected.headerchoose);
    },
    updator:function(parm){
      if(this.header.selected.headerchoose==true){
        if (parm=="info") {
          this.header.text.background='#17a2b8'
          this.header.text.color='#ffffff'
          this.header.text['font-family']='Courier New'

        }
        else if (parm=="danger") {
          this.header.text.background='#dc3545'
          this.header.text['font-family']='Lucida Sans Unicode'
          this.header.text.color='#ffffff'

        }
        else if (parm=="success") {
          this.header.text.background='#28a745'
          this.header.text['font-family']='Impact'
          this.header.text.color='#ffffff'
         
        }
      }
      if(this.body.selected.choose==true){
        if (parm=="info") {
          this.body.text.background='#17a2b8'
          this.body.text['font-family']='Courier New'
          this.body.text.color='#ffffff'

        }
        else if (parm=="danger") {
          this.body.text.background='#dc3545'
          this.body.text['font-family']='Lucida Sans Unicode'
          this.body.text.color='#ffffff'

        }
        else if (parm=="success") {
          this.body.text.background='#28a745'
          this.body.text['font-family']='Impact'
          this.body.text.color='#ffffff'
          
        }
      }
       

      this.cvmodel.headerState='load'
      //console.log(this.cvmodel);

    },
    liveCreate:function(parm){
      //this.header.text['font-family']=element
      console.log('live create')
      const header =this.header
      this.temp.header=header
      this.updator(parm) 
      
    },
    liveDestroy:function(){
      console.log('live destroy')
      this.header=this.temp.header
    },
  }

}
</script>

<style>
.themes_btn{
  font-family: 'Courier New';
}
.themes_btn2{
  font-family: 'Lucida Sans Unicode';
}
.themes_btn3{
  font-family: Impact;
}
</style>