<template>
<div>
  <div class="row">
        <ul class="nav nav-tabs">
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link " :class="nav.fichiers.class"  v-on:click="navchange('fichiers')">Fichiers</a>
        </li>
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link " :class="nav.outils.class"  v-on:click="navchange('outils')">Outils</a>
        </li>
        
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link" :class="nav.themes.class"   v-on:click="navchange('themes')" >Themes</a>
        </li>
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link" :class="nav.telecharger.class"  v-on:click="navchange('telecharger')">Telecharger</a>
        </li>
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link" :class="nav.options.class"  v-on:click="navchange('options')">Options</a>
        </li>
        <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link disabled" tabindex="-1" aria-disabled="true">Avance</a>
        </li>
        </ul>
    </div>
    <toolsbar-outils v-if="navstate=='outils'"> </toolsbar-outils>
    <toolsbar-telecharger v-if="navstate=='telecharger'"> </toolsbar-telecharger>
    <toolsbar-themes v-if="navstate == 'themes'"></toolsbar-themes>
    <toolsbar-options v-if="navstate == 'options'"></toolsbar-options>
    <toolsbar-fichiers v-if="navstate == 'fichiers'"></toolsbar-fichiers>
    
</div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'

let outils=require('./outils.vue').default
let telecharger=require('./telecharger.vue').default
let themes =require('./themes.vue').default 
let options=require('./options.vue').default 
let fichiers=require('./fichiers.vue').default

export default {
    store:store,
    components: {
        'toolsbar-outils': outils,
        'toolsbar-telecharger':telecharger,
        'toolsbar-themes':themes,
        'toolsbar-options':options,
        'toolsbar-fichiers':fichiers,
        },  
    props:['myprops'],
    data:function(){
        return {
            nav:this.$store.state.nav,
            navstate:this.$store.state.navstate,
        }
    },
     methods:{
        navchange:function (params) {
            this.navstate=params
            let activeclass='active h6 text-light bg-info'
            let disabledclass='text-dark'
            this.nav.outils.class=disabledclass
            this.nav.themes.class=disabledclass
            this.nav.telecharger.class=disabledclass
            this.nav.options.class=disabledclass
            this.nav.fichiers.class=disabledclass
            if (params=='outils') this.nav.outils.class=activeclass
            else if (params=='themes') this.nav.themes.class=activeclass
            else if (params=='telecharger') this.nav.telecharger.class=activeclass
            else if (params=='options') this.nav.options.class=activeclass
            else if (params=='fichiers') this.nav.fichiers.class=activeclass
            else console.log('ok');

        },
        debug:function(){
            console.log(this.$props)
            //axios.get('/')
        }
     }
}
</script>

<style>

</style>