<template>
  <div>
      <i class="fas fa-file-code fa-1x m-2 btn btn-danger " @click="main()" data-toggle="modal" data-target="#exampleModalCenter"></i>
        <!-- Modal -->
        <div class="modal fade" id="exampleModalCenter" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalCenterTitle">{{msg}}</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="progress" style="height: 20px;">
                  <div class="progress-bar" role="progressbar" :class="pbg" :style="{width:style}" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div>
                </div>
                <center><a  :href="url" class="btn btn-primary my-3 p-2" @click="down" :class="dbutton">telecharger</a></center>
              </div>
            </div>
          </div>
        </div>
        <!-- -->
  </div>
</template>
<script>
import store from '../../../../store/cv_data/appState.js'
const host="http://service.test/"
const api="api"
var FileSaver = require('file-saver');

export default {
    store:store,
    data:function(){
        return{
            progressbar:{
                state:20,
                style:{
                width:toString(this.state)+'%',
                }
            },
            msg:'Traitement en cours ...',
            dbutton:'disabled',
            state:0,
            style:'0%',
            pbg:'bg-primary',
            url:'#',
            down_ready:false,
            info:this.$store.state.info,
            history:this.$store.state.history,
            header:this.$store.state.header,
            body:this.$store.state.body,
            incrementeur:1,
        }
    },
    methods:{
        down:function(){
            if(this.down_ready!=true) return 0
            this.$axios.get(host+api+'/space/method/download/content/me').then((reponse)=>{
                const data=reponse.data
                var blob = new Blob([ data ], {type: "text/plain;charset=utf-8"});
                FileSaver.saveAs(blob, "MyCv.html");   
            })


        },
        ApiCreator:function(){
            let data={ 
                info:this.info,
                history:this.history,
                header:this.header,
                body:this.body,
            }
            let data_str=JSON.stringify(data)
            let secure_len=100
            for (let index = 0; index < secure_len; index++) {
                data_str=data_str.replace('#','@')   
            }
            this.$axios.get(host+api+'/space/method/send/content/'+data_str).then(()=>{
                this.incrementeur=20   
            })
        },
        main:function(){
            this.ApiCreator()
            this.lanch()
        },
        lanch:function(){
            let maxwait=50
            setInterval(() => {
                if (this.state>100) {
                this.pbg='bg-success'
                this.dbutton='enabled'
                clearInterval(this)
                //this.url=host+api+'/space/method/download/content/me'
                this.msg='pret !'
                this.down_ready=true
                return 0
                }
                if(this.state==maxwait && this.incrementeur==1){
                  this.pbg='bg-danger'
                  this.incrementeur=0
                  this.url='#'
                  this.msg='une erreur est survenue !!!'
                }
                this.state+=this.incrementeur
                this.style=this.state+'%'
                
            }, 100);
        }
    }

}
</script>
