<template>
<div class="container"> 
    <div class="row">
        <h1 class="bg-info p-5 mb-3 col-12 h1 text-light text-center">api test</h1>
        <input type="text" class="form-control col-8 m-1 p-2" id="host" aria-describedby="inputGroupPrepend" v-model="api_url" placeholder="api url to test">
        <button class="btn btn-danger mx-1" @click="pull()">Run <i class="fas fa-play "></i></button>
        <h3 class="text-success col-6 text-center m-3 italic">Result : </h3>
        <textarea name="" id="" cols="30" rows="10" v-model="recev" class="mx-3  col-12 border-0" disabled></textarea>
    </div>
    
</div>

</template>

<script>
//const host="http://service.test/"
//const api="api"
var FileSaver = require('file-saver');

export default {
    data:function(){
        return{
            recev:"",
            api_url:"",
        }
    },
    methods:{
        pull:function(){
            this.$axios.get(this.api_url).then((reponse)=>{
                 this.recev=reponse.data
                 //console.log(reponse.data);
                 var blob = new Blob(["<h1>i am html code</h1>"], {type: "text/plain;charset=utf-8"});
                 FileSaver.saveAs(blob, "cv.html");
            })
        }
    }

}
</script>

