<template>
  <div>
      <h1 class="bg-info text-light text-center p-3 rounded-bottom shadow ">votre cv est pret</h1>
      <p class=" alert alert-danger m-3 animate__bounceIn text-center text-blod" id="ajaxerr" v-if="showajaxerr"> verifier votre connexion  </p>
      <button class="btn btn-secondary" @click="back()">retour</button>
      <button class="btn btn-danger" @click="submit()">telecharger  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true" v-show="spiner1"></span>
            <span class="sr-only">...</span></button>

    <button class="btn btn-info" @click="reload()">reload</button>
  </div>
</template>

<script>
import store from '../../store/cv_data/quickcv/index.js'
const host="http://service.test/"
const api="api/"
var FileSaver = require('file-saver');

export default {
    store:store,
    data:function(){
        return{
            info:store.state.info,
            steep:store.state.steep,
            spiner1:false,
            showajaxerr:false
        }
    },
    methods:{
        back:function(){
            this.steep.now--
        },
        reload:function(){
            this.showajaxerr=true
            setTimeout(() => {
                this.showajaxerr=false
            }, 2000);
        },
        savedoc:function(data){
            var blob = new Blob([ data ], {type: "text/plain;charset=utf-8"});
            FileSaver.saveAs(blob, "MyCv.html");
        },
        submit:function(){
            //this.steep.now
            this.spiner1=true
            let data_str=JSON.stringify(this.info)
            this.$axios.get(host+api+'service/cv/quickcv/finish/'+data_str).then((reponse)=>{
                const data=reponse.data
                console.log(data) 
                this.spiner1=false
                this.savedoc(data)
        }).catch((err)=>{
                console.log(err);
                this.reload()
                this.spiner1=false
            })
        }

    }

}
</script>
