<template>
  <div  class="row my-3 shadow mb-4 mt-1 rounded-bottom p-1">
        <i class="fas fa-file fa-1x m-2 btn btn-info"></i>
        <i class="fas fa-file-archive fa-1x m-2 btn btn-info"></i>
        <in-html/>
        <i class="fas fa-file-pdf fa-1x m-2 btn btn-info"></i>
        <i class="fas fa-file-export fa-1x m-2 btn btn-info"></i>
        <i class="fas fa-file-import fa-1x m-2 btn btn-info"></i>
</div>
</template>

<script>
import store from '../../../store/cv_data/appState.js'

const InHtml =require('./telechargement/InHtml.vue').default
export default {
  store:store,
  components:{
    InHtml:InHtml,
  },
  data:function(){
    return{
      progressbar:{
        state:20,
        style:{
           width:toString(this.state)+'%',
        }
      },
      dbutton:'disabled',
      state:0,
      style:'0%',
      pbg:'bg-primary',
      url:'#',
    }
  },

  methods:{
    lanch:function(){
      setInterval(() => {
        if (this.state>100) {
          //this.state=0
          this.pbg='bg-success'
          this.dbutton='enabled'
          clearInterval(this)
          this.url="http:fuckyou"
          return 0
        }
        this.state+=10
        this.style=this.state+'%'
        
      }, 100);
    }
  },
}
</script>