<template>
  <router-view/>
  
</template>

<script>
import VueRouter from 'vue-router'
import Vue from 'vue'
import comploader from './helper/compLoader.js'

Vue.use(VueRouter)

// route de l'application 

const router =new VueRouter({
    mode:'hash',
    routes:[{
        path:'/my-cv',
        component:require('./components/cvmake/main.vue').default,
        name:'cv',
    },
    {
        path:'/choose',
        component:require('./components/home/choose.vue').default,
        name:'choose',
    },
    {
        path:'/quick-cv',
        component:require('./components/quickcv/index.vue').default,
        name:'quickcv',
    },
    {
        path:'/debug',
        component:require('./components/debug/api_test.vue').default,
        name:'debug',
    },
    {
        path:'/',
        component:comploader('home/index'),
        name:'home',
        children:[
            {
            path:'/login',
            component:require('./components/home/login.vue').default,
            name:'home.login',
            },
            {
            path:'/register',
            component:require('./components/home/register.vue').default,
            name:'home.register',
            }
        ]
    },
]
})

console.log(router)
export default {
    name:'app',
    router:router
}
</script>