<template>
  <i class="fas fa-trash fa-1x m-2 btn btn-danger" v-on:click='trash' ></i>
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            info:this.$store.state.info,
        }    
    },
    methods:{
         trash: function(){
            if (this.info.name==' ' && this.info.username==' ' ) {
                return 0
            }
            this.info.name=''
            this.info.username=''
           // this.update_formu()
        },
    },

}
</script>

