<template>
    <div class="col-3 row">
    <label for="customRange1" class="text-center col-7">opaciter ({{opaciter}}%)</label>
    <input type="range" class="custom-range col-5" id="customRange1" v-model="opaciter" value="100">     
    </div>
</template>

<script>
import store from '../../../../store/cv_data/appState.js'
export default {
    store:store,
    data:function(){
        return{
            opaciter:this.$store.state.opaciter,
            header:this.$store.state.header,
            body:this.$store.state.body
        }
    },
    watch:{
        opaciter:function () {
            if (this.body.selected.choose==true) {
                 this.body.text.opacity=this.opaciter/100
             }
             if (this.header.selected.headerchoose==true) {
                 this.header.text.opacity=this.opaciter/100
             }
            //this.header.text.opacity=this.opaciter/100
        },  
    }

}
</script>